import { Component, OnInit } from '@angular/core';
import { Pauch } from '../models/pauch.model';
import { UserServices } from 'src/app/user.services';

@Component({
  selector: 'app-pauch-history',
  templateUrl: './pauch-history.component.html',
  styleUrls: ['./pauch-history.component.css']
})
export class PauchHistoryComponent implements OnInit {
  pauchHistory: Pauch[];
  totalTips = 0;
  avrgTipsInDay = 0;
  constructor(private userServices: UserServices) { }

  ngOnInit() {
    this.pauchHistory = this.userServices.getPauchHistory();
    this.getTotalInfo();

  }

  getTotalInfo() {
    for (let i = 0; i < this.pauchHistory.length; i++) {
      this.totalTips += Number(this.pauchHistory[i].amount);
    }
    if (this.pauchHistory.length > 0) {
      this.avrgTipsInDay = this.totalTips / this.pauchHistory.length;
      // todo make it work
      // this.avrgTipsInDay = this.avrgTipsInDay.toFixed(2);
    } else {
      this.totalTips = this.userServices.getCurrentPauch();
      this.avrgTipsInDay = 0;
    }
  }

  onRemove(pauchIndex: number) {
    this.userServices.onRemoveFromPauchHistory(pauchIndex);
    this.pauchHistory = this.userServices.getPauchHistory();
    this.getTotalInfo();
  }

}
